import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Box,
  Button,
  Container,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Settings,
  Palette,
  LightMode,
  DarkMode,
} from '@mui/icons-material';
import { APISettings } from './APISettings';
import { APIConfig, saveAPIConfig } from '../config/api';
import { useTheme } from '../theme';
import logo from '/mgen_logo.svg';

// ===========================================
// MinutesGen v1.0 - アプリケーションヘッダー
// ===========================================

interface AppHeaderProps {
  onRestart?: () => void;
}

export const AppHeader: React.FC<AppHeaderProps> = ({ onRestart }) => {
  const { themeMode, toggleTheme } = useTheme();
  const [settingsOpen, setSettingsOpen] = useState(false);
  const navigate = useNavigate();

  const handleSettingsOpen = () => {
    setSettingsOpen(true);
  };

  const handleSettingsClose = () => {
    setSettingsOpen(false);
  };

  const handleSettingsSave = (config: APIConfig) => {
    try {
      saveAPIConfig(config);
      console.log('API設定を保存しました:', config);
    } catch (error) {
      console.error('API設定の保存に失敗しました:', error);
    }
  };

  const handleLogoClick = () => {
    if (onRestart) {
      onRestart();
    }
    navigate('/');
  };

  return (
    <AppBar
      position="static"
      sx={{
        background: themeMode === 'light'
          ? 'linear-gradient(135deg, #f5f5f5 0%, #e8f0fe 100%)'
          : 'linear-gradient(135deg, #424242 0%, #212121 100%)',
        boxShadow: themeMode === 'light'
          ? '0 4px 20px rgba(25, 118, 210, 0.2)'
          : '0 4px 20px rgba(66, 66, 66, 0.4)',
      }}
    >
      <Container maxWidth="lg">
        <Toolbar sx={{ py: 2, minHeight: '80px !important' }}>
          {/* ロゴセクション */}
          <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                transition: 'transform 0.3s ease',
                cursor: 'pointer',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
              onClick={handleLogoClick}
            >
              <img
                src={logo}
                alt="MinutesGen Logo"
                style={{
                  height: '60px',
                  width: 'auto',
                  filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.2))',
                }}
              />
            </Box>
          </Box>

          {/* ナビゲーション・アクションセクション */}
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            <Tooltip title="API設定">
              <IconButton
                onClick={handleSettingsOpen}
                sx={{
                  color: themeMode === 'light' ? 'primary.main' : 'white',
                  backgroundColor: 'rgba(255, 255, 255, 0.1)',
                  '&:hover': {
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                    transform: 'translateY(-2px)',
                  },
                  transition: 'all 0.3s ease',
                  borderRadius: 2,
                }}
              >
                <Settings />
              </IconButton>
            </Tooltip>

            <Tooltip title={`テーマ切り替え (現在: ${themeMode === 'light' ? 'ライト' : 'ダーク'})`}>
              <IconButton
                onClick={toggleTheme}
                sx={{
                  color: themeMode === 'light' ? 'primary.main' : 'white',
                  backgroundColor: 'rgba(255, 255, 255, 0.1)',
                  '&:hover': {
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                    transform: 'translateY(-2px)',
                  },
                  transition: 'all 0.3s ease',
                  borderRadius: 2,
                }}
              >
                {themeMode === 'light' ? <LightMode /> : <DarkMode />}
              </IconButton>
            </Tooltip>
          </Box>
        </Toolbar>
      </Container>
      
      {/* API設定画面 */}
      <APISettings
        open={settingsOpen}
        onClose={handleSettingsClose}
        onSave={handleSettingsSave}
      />
    </AppBar>
  );
}; 